import React from 'react';
import { motion } from 'framer-motion';
import { ShieldCheckIcon, CodeBracketIcon, LockClosedIcon } from '@heroicons/react/24/outline';
import TypingAnimation from './TypingAnimation';

// Animation variants for text elements
const textVariants = {
  hidden: { opacity: 0, y: 20 },
  visible: (i: number) => ({
    opacity: 1,
    y: 0,
    transition: {
      delay: i * 0.2,
      duration: 0.6,
      ease: "easeOut"
    }
  })
};

const Hero = () => {
  const typingTexts = [
    "Cybersecurity Analyst",
    "SOC Operations Specialist", 
    "Threat Hunter",
    "Penetration Tester",
    "Security Researcher"
  ];

  return (
    <section 
      id="home" 
      className="min-h-screen flex items-center pt-20 md:pt-24 bg-gradient-to-br from-background-light via-background-light/95 to-background-light/90 dark:from-background-dark dark:via-gray-900/50 dark:to-background-dark/80 relative overflow-hidden"
    >
      {/* Cyber Grid Background */}
      <div className="absolute inset-0 cyber-grid dark:opacity-20 opacity-10"></div>
      
      <div className="container mx-auto px-4 sm:px-6 lg:px-8 py-12 md:py-16 relative z-10"> 
        <div className="max-w-6xl mx-auto">
          
          {/* Terminal Window */}
          <motion.div
            initial={{ opacity: 0, y: 30 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.8 }}
            className="terminal max-w-4xl mx-auto"
          >
            {/* Terminal Header */}
            <div className="terminal-header">
              <div className="flex items-center space-x-2">
                <div className="terminal-button red"></div>
                <div className="terminal-button yellow"></div>
                <div className="terminal-button green"></div>
              </div>
              <div className="flex-1 text-center">
                <span className="text-sm font-mono text-terminal-green">dulitha@cybersecurity:~$</span>
              </div>
            </div>

            {/* Terminal Content */}
            <div className="terminal-text space-y-4">
              <motion.div
                initial={{ opacity: 0 }}
                animate={{ opacity: 1 }}
                transition={{ delay: 0.5, duration: 0.5 }}
                className="flex items-center space-x-2"
              >
                <span className="text-terminal-green">$</span>
                <span className="text-electric-blue">whoami</span>
              </motion.div>
              
            <motion.h1 
              initial="hidden"
              animate="visible"
                custom={0}
              variants={textVariants}
                className="text-3xl sm:text-4xl md:text-5xl lg:text-6xl font-cyber font-bold text-terminal-green mb-4"
            >
              DULITHA WICKRAMASINGHE
            </motion.h1>

              <motion.div
                initial={{ opacity: 0 }}
                animate={{ opacity: 1 }}
                transition={{ delay: 1, duration: 0.5 }}
                className="flex items-center space-x-2 mb-6"
              >
                <span className="text-terminal-green">$</span>
                <span className="text-electric-blue">cat role.txt</span>
              </motion.div>

              <motion.div
                initial={{ opacity: 0 }}
                animate={{ opacity: 1 }}
                transition={{ delay: 1.5, duration: 0.5 }}
                className="text-2xl sm:text-3xl font-mono text-electric-blue mb-8 min-h-[2.5rem]"
              >
                <TypingAnimation 
                  texts={typingTexts}
                  speed={100}
                  deleteSpeed={50}
                  pauseTime={2000}
                />
              </motion.div>

              <motion.div
                initial={{ opacity: 0 }}
                animate={{ opacity: 1 }}
                transition={{ delay: 2, duration: 0.5 }}
                className="flex items-center space-x-2 mb-6"
              >
                <span className="text-terminal-green">$</span>
                <span className="text-electric-blue">cat about.txt</span>
              </motion.div>

            <motion.p 
              initial="hidden"
              animate="visible"
                custom={1}
              variants={textVariants}
                className="text-lg text-primary-light dark:text-primary-dark leading-relaxed mb-8 max-w-3xl"
              >
                Cybersecurity professional with expertise in <span className="text-terminal-green font-semibold">SOC operations</span>, 
                <span className="text-electric-blue font-semibold"> threat hunting</span>, 
                <span className="text-electric-purple font-semibold"> endpoint security</span>, and 
                <span className="text-terminal-green font-semibold"> penetration testing</span>. 
                Skilled in Palo Alto Cortex XDR, Entra ID, Identity Protection, and email security. 
                Passionate about safeguarding digital assets through innovative security strategies.
            </motion.p>

              {/* Action Buttons */}
              <motion.div 
                initial="hidden"
                animate="visible"
                custom={2}
                variants={textVariants}
                className="flex flex-wrap gap-4 mb-6"
              >
                <motion.a
                  href="#projects"
                  className="btn btn-primary group"
                  whileHover={{ scale: 1.05, y: -2 }} 
                  whileTap={{ scale: 0.95 }}
                >
                  <CodeBracketIcon className="w-5 h-5 mr-2 group-hover:animate-pulse" />
                  View Projects
                </motion.a>
                <motion.a
                  href="#blogs"
                  className="btn btn-secondary group"
                  whileHover={{ scale: 1.05, y: -2 }} 
                  whileTap={{ scale: 0.95 }}
                >
                  <ShieldCheckIcon className="w-5 h-5 mr-2 group-hover:animate-pulse" />
                  Read Blog
                </motion.a>
                <motion.a
                  href="#contact"
                  className="btn btn-tertiary group"
                  whileHover={{ scale: 1.05, y: -2 }} 
                  whileTap={{ scale: 0.95 }}
                >
                  <LockClosedIcon className="w-5 h-5 mr-2 group-hover:animate-pulse" />
                  Contact Me
                </motion.a>
              </motion.div>

              {/* Social Links */}
              <motion.div
                initial={{ opacity: 0 }}
                animate={{ opacity: 1 }}
                transition={{ delay: 2.5, duration: 0.5 }}
                className="flex items-center space-x-2"
              >
                <span className="text-terminal-green">$</span>
                <span className="text-electric-blue">ls social/</span>
              </motion.div>

            <motion.div 
              initial="hidden"
              animate="visible"
                custom={3}
              variants={textVariants}
                className="flex flex-wrap gap-4"
            >
              <motion.a
                href="https://www.linkedin.com/in/dulitha-wickramasinghe-398971211/"
                target="_blank"
                rel="noopener noreferrer"
                  className="flex items-center space-x-2 text-electric-blue hover:text-terminal-green transition-colors duration-300 group"
                  whileHover={{ scale: 1.05 }}
              >
                  <span className="font-mono">linkedin.com</span>
                  <span className="text-terminal-green group-hover:animate-pulse"></span>
              </motion.a>
              <motion.a
                href="https://github.com/Dulitha99"
                target="_blank"
                rel="noopener noreferrer"
                  className="flex items-center space-x-2 text-electric-blue hover:text-terminal-green transition-colors duration-300 group"
                  whileHover={{ scale: 1.05 }}
              >
                  <span className="font-mono">github.com</span>
                  <span className="text-terminal-green group-hover:animate-pulse"></span>
              </motion.a>
            </motion.div>
            
              {/* Terminal Cursor */}
          <motion.div 
                initial={{ opacity: 0 }}
                animate={{ opacity: 1 }}
                transition={{ delay: 3, duration: 0.5 }}
                className="flex items-center space-x-2 mt-4"
              >
                <span className="text-terminal-green">$</span>
                <motion.span
                  animate={{ opacity: [1, 0] }}
                  transition={{ duration: 1, repeat: Infinity, repeatType: 'reverse' }}
                  className="w-2 h-5 bg-terminal-green"
                />
              </motion.div>
            </div>
          </motion.div>
        </div>
      </div>
    </section>
  );
};

export default Hero; 
